*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
*::before{
    box-sizing: border-box;
}
*::after{
    box-sizing: border-box;
}
#html{
    word-break: break-all;
}
#div1 {
    position: fixed;
    left:50%;
    transform: translate(-50%);
    top:20px;
}
#div1::before{
    content: '';
    display: block;
}
#div1::after{
    content: '';
    display: block;
}
@media(max-width:500px){
    #html{
        height: 50vh;
        overflow: hidden;
    }
    #div1Wrapper{
        height: 50vh;
    }
    #div1{
        position: relative;
        top: 0;
        right: 0;
    }
}